.profile-container {
  font-family: 'Arial', sans-serif;
}

header h1 {
  text-align: left;
  margin: 30px;
}

.user-img-name{
  display: flex;
  align-items: center;
}

.user-info {
  display: flex;
  align-items: center;
  margin: 30px;
  justify-content: space-between;
}

.user-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

.text-info h4, .text-info p {
  margin: 0;


}

.action-buttons {
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 10px;
}

.action-buttons button {
  margin: 5px 0;
}

.profile-actions {
  list-style: none;
  padding: 0;
}

.profile-actions li a{
  text-decoration: none;
  color: inherit; /*     */
  padding: 15px;
  margin: 0 20px;
  border-radius: 10px;
  justify-content: space-between;
  display: flex; /* Flexbox  */
}

.profile-actions li a:hover {
  text-decoration: underline; /*     */
}

.profile-actions li a:active {
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2); /* x y      */
}



.register-button {
  background-color: white; /*  */
  border: none; /*   */
  padding: 10px 10px; /*  10px,  20px  */
  border-radius: 10px; /*   */
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2); /*   */
  font-size: 16px; /*   */
  cursor: pointer; /*    */
  display: flex; /* Flexbox  */
  align-items: center; /*    */
  /* justify-content: center;    */
  margin:20px; /*  0,  5px  */
  text-align: left;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif
}

.register-button img{
    width: 70px;
    border-radius: 10px;
}


