/*   */
.title {
    text-align: center;
    margin-top: 15px;
}

/*   */
.container {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    margin-right: 10px;
    margin-left: 10px;

    border-radius: 15px;
}

/*    */
.left {
    flex: 1;
    padding: 5px;
}

/*    */
.right {
    flex: 1;
    padding: 5px;
    height: 100px;
}

/*   */
.average {
    font-weight: initial;
    font-size: 40px;
    margin-bottom: 20px;
    margin-left: -10px;
    margin-right: 30px;
    margin-top: -10px;
    padding: 10px;
    text-align: center;
    border-radius: 15px;
}

/*    */
.starContainer {
    display: flex;
    justify-content: center;
    margin-top: -30px;
    margin-left: -30px;
}

/*    */
.gaugeContainer {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

/*   */
.gauge {
    height: 14px;
    background-color: #405DE6;
    border-radius: 15px;
}

/*   */
.label {
    width: 10px;
    text-align: right;
    margin-right: 10px;
    border-radius: 15px;
    margin-top: -2px;
    margin-left: -10px;
}

/*    */
.gaugeBackground {
    width: 100%;
    height: 14px;
    background-color: #e4e5e9;
    position: relative;
    border-radius: 10px;
}

/*   */
.percentage {
    position: absolute;
    right: 0;
    top: 0;
    font-size: 9px;
    height: 15px;
    display: flex;
    align-items: center;
}

/*    */
.inputTitle {
    display: flex;
    width: 94%;
    height: 30px;
    justify-content: space-between;
    border: 2.5px solid gray;
    border-radius: 15px;
    margin-top: 40px;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 15px;
}

/*    */
.inputContext {
    display: flex;
    width: 94%;
    height: 60%;
    justify-content: space-between;
    border: 2.5px solid gray;
    border-radius: 15px;
    margin-top: 20px;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 20px;
}

/*   */
.button {
    background-color: #405DE6;
    color: white;
    border-radius: 10px;
    padding: 10px;
    cursor: pointer;
    margin-top: 40px;
    margin-bottom: 20px;
    text-align: center;
    margin-left: 20px;
}

/*    */
.buttonDiv {
    display: flex;
    justify-content: center;
    margin-top: 21px;
    margin-bottom: 40px;
    margin-left: -20px;
}
