/* Category css  */
.container {
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f5f5;
}

.category {
  display: flex;
  font-family: Arial, sans-serif;
  height: 100vh;
  margin: 0;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.category .section {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}

.category .iconBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 45%; /*    */
  padding: 10px;
  border: 1px solid #ccc; /*   */
  border-radius: 5px; /*     */
}

.category .icon-box p {
  text-align: center;
}