.content {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    gap: 10px; /*    */
    padding: 20px;
    background-color: #f7f9fc;
}

.content .roundedInput {
    width: 100%;
    padding: 10px;
    margin: 5px 0;
    border: 1px solid #ccc; /*  */
    border-radius: 10px; /*   */
    resize: vertical; /*     */
    font-size: 16px; /*   */
}

.content h3 {
    margin: 0;
    padding: 10px 0;
    color: #333;
    font-size: 20px;
}

.content .large {
    height: 150px; /*      . */
}